<template>
  <div>
    <el-menu
        class="el-menu-homepage"
        mode="horizontal"
        background-color="#1F4B62"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="text-align: left">
      <el-tooltip class="item" effect="dark" content="点击回到主页" placement="bottom">
        <el-menu-item index="10" @click="backtoHomepage()" style="width: 180px;float: left">
          回到主页
        </el-menu-item>
      </el-tooltip>
    </el-menu>
    <el-container style="height: calc(100vh - 80px); border: 1px solid #eee">
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1', '3']">
          <el-submenu index="1">
            <template v-slot:title><i class="el-icon-message"></i>何</template>
            <el-menu-item-group>
              <router-link to="/personinfo"><el-menu-item index="1-1">用户信息</el-menu-item></router-link>
              <router-link to="/changeinfo"><el-menu-item index="1-2">修改信息</el-menu-item></router-link>
              <el-submenu index="10">
                <template v-slot:title>
                  我的订单
                </template>
                <el-menu-item index="20">
                  <router-link to="/orderwaitpay">
                    待付款订单
                  </router-link>
                </el-menu-item>
                <el-menu-item index="30">
                  <router-link to="/ordercheckedin">
                    已入住订单
                  </router-link>
                </el-menu-item>
                <el-menu-item index="40">
                  <router-link to="/orderwillcancel">
                    待取消订单
                  </router-link>
                </el-menu-item>
                <el-menu-item index="50">
                  <router-link to="/ordercanceled">
                    已取消订单
                  </router-link>
                </el-menu-item>
              </el-submenu>
              <router-view/>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header style="text-align: right;font-size: 12px">
          <router-link to="/personinfo">回到首页</router-link>
        </el-header>
        <el-main>
          <el-table :data="tableData" empty-text="无已取消订单">
            <el-table-column prop="o_id" label="订单号" width="140">
            </el-table-column>
            <el-table-column prop="u_id" label="用户id" width="140">
            </el-table-column>
            <el-table-column prop="h_id" label="酒店id" width="140">
            </el-table-column>
            <el-table-column prop="rtname" label="房型id" width="140">
            </el-table-column>
            <el-table-column prop="Creat_time" label="创建日期" width="200">
            </el-table-column>
            <el-table-column prop="state" label="订单状态" width="80">
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "ordercanceled",
  data () {
    return {
      item: [],
      tableData: []
    }
  },
  methods:{
    backtoHomepage () {
      this.$router.push('/homepage')
    },
    ordercanceled () {
      const self = this
      this.$axios({
        url: '/api/orderList/ListByState',
        method: 'POST',
        params: {
          pageNo: 1,
          pageSize: 10,
          state: '已取消'
        }
      }).then(
          successResponse => {
            if (successResponse.data.data) {
              self.tableData = successResponse.data.data
              // console.log(self.tableData)
              // eslint-disable-next-line no-empty
            } else {
            }
          }).catch(err => {
        console.log(err, 'err')
      })
    },
    $axios(param) {

    },
    create(){
      this.ordercanceled()
    }
  }
}
</script>

